


































import md5 from "@/common/SDK/md5.min.js";
export default {
	data() {
		return {
			phoneNumber: '',
			passwd:'',
			isShowOauth:false,
			showProvider:{
				weixin:false,
				qq:false,
				sinaweibo:false,
				xiaomi:false
			}
		}
	},
	onShow() {
		
	},
	onLoad() {
		//APP显示第三方登录



		this.getProvider();
		
	}, 
	methods: {
		oauthLogin(provider){
			uni.showLoading();
			//第三方登录
			uni.login({
				provider: provider,
				success: (loginRes)=>{
					console.log("success: "+JSON.stringify(loginRes));
					//案例直接获取用户信息，一般不是在APP端直接获取用户信息，比如微信，获取一个code，传递给后端，后端再去请求微信服务器获取用户信息
					uni.getUserInfo({
						provider: provider,
						success: (infoRes)=>{
							console.log('用户信息：' + JSON.stringify(infoRes.userInfo));
							uni.setStorage({
								key: 'UserInfo',
								data: {
									username:infoRes.userInfo.nickName,
									face:infoRes.userInfo.avatarUrl,
									signature:'个性签名',
									integral:0,
									balance:0,
									envelope:0
								},
								success: function () {
									uni.hideLoading()
									uni.showToast({title: '登录成功',icon:"success"});
									setTimeout(function(){
										uni.navigateBack();
									},300)
								}
							});
						}
					});
				},
				fail:(e)=>{
					console.log("fail: "+JSON.stringify(e));
				}
			});
		},
		getProvider(){
			//获取第三方登录服务
			uni.getProvider({
				service: 'oauth',
				success: (res)=>{
					let len = res.provider.length;
					for(let i=0;i<len;i++){
						//有服务才显示按钮图标
						this.showProvider[res.provider[i]] = true;
					}
					if(res.provider.length==0){
						this.isShowOauth=false;
					}
				}
			});
		},
		toPage(page){
			uni.hideKeyboard()
			uni.navigateTo({
				url: page
			});
		},
		doLogin(){
			uni.hideKeyboard();
			//验证手机号码
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber))){ 
				uni.showToast({title: '请填写正确手机号码',icon:"none"});
				return false; 
			}
			uni.showLoading({
				title: '提交中...'
			})
			//模板示例比对本地储存的用户信息，实际使用中请替换为上传服务器比对。
			setTimeout(()=>{
				let md5PW = md5(this.passwd)
				uni.getStorage({
					key: 'UserList',
					success: (res)=>{
						for(let i in res.data){
							let row = res.data[i];
							if(row.username==this.phoneNumber){
								uni.hideLoading()
								//比对密码
								if(md5PW == res.data[i].passwd){
									uni.showToast({title: '登录成功',icon:"success"});
								}else{
									uni.showToast({title: '账号或密码不正确',icon:"none"});
								}
							}
						}
					},
					fail:function(e){
						uni.hideLoading()
						uni.showToast({title: '手机号码未注册',icon:"none"});
					}
				});
			},1000)
		}
	}
}
